<template>
  <div>
    <el-autocomplete
      style="width:100%;"
      popper-class="autoAddressClass"
      v-model="form.address"
      :fetch-suggestions="querySearchAsync"
      :trigger-on-focus="false"
      placeholder=""
      @select="handleSelect"
      clearable
      @focus="showMap"
      @click="vanish"
    >
      <template slot-scope="{ item }">
        <i class="el-icon-search fl mgr10"></i>
        <div style="overflow:hidden;">
          <div class="title">{{ item.title }}</div>
          <span class="address ellipsis">{{ item.address }}</span>
        </div>
      </template>
    </el-autocomplete>
    <div :v-if="!show" id="map-container" class="vanish" style="width:100%;height:350px;"></div>
  </div>
</template>

<script>
import loadMap from "../../../utils/loadMap";
export default {
  name: "amap",
  isRun: false,
  data() {
    return {
      show: false,
      form: {
        address: "", //
        addrPoint: {
          //
          lng: 0,
          lat: 0
        }
      },
      map: "", //
      mk: "" //Marker
    };
  },
  methods: {
    async showMap() {
      $("#map-container").removeClass("vanish");
      if (!this.isRun) {
        await loadMap("N5Gz0uOiHk8r9fnA9pQ9M8nzWM7lcqaG"); //BMap
        this.initMap();
      }
      this.isRun = true;
    },
    vanish() {
      this.$emit("getposition", this.form.address, this.form.addrPoint);
      $("#map-container").addClass("vanish");
    },
    initMap() {
      var that = this;
      this.map = new BMap.Map("map-container", { enableMapClick: false }); //enableMapClick:false 
      var point = new BMap.Point(113.30765, 23.12005);
      this.map.centerAndZoom(point, 19);
      this.mk = new BMap.Marker(point, { enableDragging: true }); //enableDraggingfalse
      this.map.addOverlay(this.mk); //
      this.mk.addEventListener("dragend", function(e) {
        that.getAddrByPoint(e.point); //e.point
      });
      var navigationControl = new BMap.NavigationControl({
        //
        anchor: BMAP_ANCHOR_TOP_RIGHT, //
        type: BMAP_NAVIGATION_CONTROL_SMALL //SMALL
      });
      this.map.addControl(navigationControl); //
      var geolocationControl = new BMap.GeolocationControl({
        anchor: BMAP_ANCHOR_BOTTOM_LEFT
      }); //
      geolocationControl.addEventListener("locationSuccess", function(e) {
        //
        that.getAddrByPoint(e.point); //
      });
      geolocationControl.addEventListener("locationError", function(e) {
        //
        alert(e.message);
      });
      this.map.addControl(geolocationControl); //
      //
      //this.geolocation();
      this.map.addEventListener("click", function(e) {
        //
        that.getAddrByPoint(e.point); //
      });
    },
    sendPos(address, point) {
      this.$emit("getposition", address, point);
    },
    getAddrByPoint(point) {
      var that = this;
      var geco = new BMap.Geocoder();
      geco.getLocation(point, function(res) {
        // console.log(res); //
        that.mk.setPosition(point); //
        that.map.panTo(point); //
        that.form.address = res.address; //
        that.form.addrPoint = point; //

        that.sendPos(res.address, point);
      });
    },
    geolocation() {
      var that = this;
      var geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(
        function(res) {
          if (this.getStatus() == BMAP_STATUS_SUCCESS) {
            that.getAddrByPoint(res.point); //
          } else {
            //alert("failed" + this.getStatus()); //
            // console.log("failed" + this.getStatus())
          }
        },
        { enableHighAccuracy: true }
      ); //enableHighAccuracyfalse
    },
    querySearchAsync(str, cb) {
      var options = {
        onSearchComplete: function(res) {
          //
          var s = [];
          if (local.getStatus() == BMAP_STATUS_SUCCESS) {
            for (var i = 0; i < res.getCurrentNumPois(); i++) {
              s.push(res.getPoi(i));
            }
            cb(s); //cb<el-autocomplete>
          } else {
            cb(s);
          }
        }
      };
      var local = new BMap.LocalSearch(this.map, options); //LocalSearch
      local.search(str); //searchstr
    },
    handleSelect(item) {
      this.form.address = item.address + item.title; //
      this.form.addrPoint = item.point; //
      this.map.clearOverlays(); //
      this.mk = new BMap.Marker(item.point); //Marker
      this.map.addOverlay(this.mk); //
      this.map.panTo(item.point); //
    }
  },
  //async/await BMap
  async mounted() {
    // await loadMap("8K3RjFBltDcKqGxKLKsYyxbYVzPKG9hN"); //BMap
    // this.initMap();
  }
};
</script>

<style scoped>
.el-icon-search {
  margin-top: 11px;
}
.mgr10 {
  margin-right: 10px;
}
.title {
  text-overflow: ellipsis;
  overflow: hidden;
}
.address {
  line-height: 1;
  font-size: 12px;
  color: #b4b4b4;
  margin-bottom: 5px;
}
.vanish {
  display: none;
}
</style>



